@import url(https://fonts.googleapis.com/css2?family=Sunflower:wght@300;500;700&display=swap);
* {
  margin: 0;
  padding: 0;
  font-family: 'Sunflower', sans-serif;
}

li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #000;
}

nav{display:flex;justify-content:space-between;align-items:center;height:3rem}nav>h1{margin-left:1rem}nav>ul{display:flex;align-items:center;height:100%}nav>ul>li{width:6rem;text-align:center}

header{position:relative;overflow-x:hidden}header .slide-wrap{display:flex;transition:-webkit-transform 0.35s ease-in-out;transition:transform 0.35s ease-in-out;transition:transform 0.35s ease-in-out, -webkit-transform 0.35s ease-in-out;height:40vh}header .slide-wrap>li{position:relative}header .slide-wrap>li>img{object-fit:cover;object-position:center center;width:100vw;height:40vh}header .slide-wrap>li .list-info{position:absolute;top:50%;left:5rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}header .slide-wrap>li .list-info>h2{text-shadow:2px 2px 5px #fff}header .slide-wrap>li .list-info>p{margin-bottom:1rem;text-shadow:1px 1px 3px #fff}header .slide-wrap>li .list-info>a{padding:0.5rem 1.5rem;background-color:rgba(255,255,255,0.5)}header .side-arrow-wrap{display:flex;position:absolute;justify-content:space-between;top:50%;width:100%;color:#fff;font-size:4rem;-webkit-transform:translateY(-50%);transform:translateY(-50%)}header .side-arrow-wrap>svg{cursor:pointer;display:none;background-color:rgba(0,0,0,0.4)}header:hover>div>svg{display:block}header .slide-length{position:absolute;bottom:0;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%)}header .slide-length>button{cursor:pointer;margin:1rem;width:20px;height:20px;border-radius:50%;border:0;color:transparent;background-color:#aaa}header .slide-length>.slideBtn{background-color:#000}

.container1{padding:5vw 10vh}.container1>h2{font-size:2rem;margin-bottom:5rem}.container1 .hot-issue-wrap{display:flex;justify-content:space-between}.container1 .hot-issue-wrap>li{width:23%}.container1 .hot-issue-wrap>li>a>img{width:100%}.container1 .hot-issue-wrap>li>.image-content{position:relative}.container1 .hot-issue-wrap>li>.image-content>h3{line-height:3rem}.container1 .hot-issue-wrap>li>.image-content>.like-count{position:absolute;display:flex;align-items:center;top:0.7rem;right:0}.container1 .hot-issue-wrap>li>.image-content>.hot-issue-content{overflow:hidden;text-overflow:ellipsis;white-space:nowrap}

